"use client";

import { Cloud, CloudRain, Sun, Wind, Droplets } from "lucide-react";
import { Card } from "@/components/ui/card";
import { useEffect, useRef, useState } from "react";

import { WeatherData } from "@weather-app/types";
import LoadingSkeleton from "../LoadingSkeleton";

export default function WeatherDashboard() {
  const [city, setCity] = useState("New Delhi, India");
  const [weatherData, setWeatherData] = useState<WeatherData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  const inputRef = useRef<HTMLInputElement>(null);

  const mirrorRef = useRef<HTMLSpanElement>(null);

  useEffect(() => {
    inputRef.current?.focus();
  }, []);

  useEffect(() => {
    if (mirrorRef.current && inputRef.current) {
      inputRef.current.style.width = `${mirrorRef.current.offsetWidth + 0}px`;
    }
  }, [city]);

  useEffect(() => {
    const fetchWeather = async () => {
      try {
        setLoading(true);
        setError("");
        const response = await fetch(
          `/api/weather?city=${encodeURIComponent(city)}`,
        );
        if (!response.ok) {
          throw new Error("Failed to fetch weather data");
        }
        const data = await response.json();
        setWeatherData(data.data);
      } catch (err) {
        setError("Failed to load weather data");
      } finally {
        setLoading(false);
      }
    };

    const timer = setTimeout(fetchWeather, 500);
    return () => clearTimeout(timer);
  }, [city]);

  const getWeatherIcon = (description: string) => {
    if (description?.includes("rain")) return CloudRain;
    if (description?.includes("clear")) return Sun;
    return Cloud;
  };

  const WeatherIcon = weatherData
    ? getWeatherIcon(weatherData.description)
    : Cloud;

  return (
    <Card className="w-full max-w-3xl p-6 space-y-6 bg-card border-none shadow-none text-card-foreground">
      <div className="text-muted-foreground text-2xl">
        <span>Right now in </span>
        <span className="relative font-medium text-foreground inline-flex items-center">
          <input
            ref={inputRef}
            className="outline-none border-none focus:outline-none bg-transparent p-0 ml-1"
            value={city}
            onChange={(e) => setCity(e.target.value)}
          />
          <span
            ref={mirrorRef}
            className="absolute invisible top-full whitespace-nowrap px-[2px]"
          >
            {city || " "}
          </span>
        </span>
        <span>, it&apos;s {weatherData?.description || "loading..."}</span>
      </div>

      {error ? (
        <div className="text-red-500">{error}</div>
      ) : loading ? (
        <LoadingSkeleton />
      ) : (
        <>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <WeatherIcon className="w-16 h-16 text-muted-foreground" />
              <div className="text-6xl font-light">
                {weatherData
                  ? `${Math.round(weatherData.temperature)}°`
                  : "--°"}
              </div>
            </div>
            <div className="space-y-2">
              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <Wind className="w-4 h-4" />
                <span>
                  {weatherData ? `${weatherData.windSpeed} m/s` : "--"}
                </span>
              </div>
              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <Droplets className="w-4 h-4" />
                <span>{weatherData ? `${weatherData.humidity}%` : "--"}</span>
              </div>
            </div>
          </div>

          <div className="grid grid-cols-4 gap-4 pt-4 border-t border-border">
            {[
              { day: "TOM", icon: CloudRain },
              { day: "FRI", icon: Sun },
              { day: "SAT", icon: Sun },
              { day: "SUN", icon: Sun },
            ].map((forecast) => (
              <div key={forecast.day} className="text-center space-y-2">
                <div className="text-sm font-medium text-foreground">
                  {forecast.day}
                </div>
                <forecast.icon className="w-6 h-6 mx-auto text-muted-foreground" />
              </div>
            ))}
          </div>
        </>
      )}
    </Card>
  );
}
